$color-background: #2a2b2f;
$color-background-80: #2a2b2fee;
$color-font: #a8b7c3;
$color-font-a: #e6c06c;
$color-font-b: #2998db;
$color-font-c: #69875a;
$player-header-height: 30px;
$player-list-height: 88px;

.Header,
.Footer,
.page-group {
    display: none;
}

.PageMiniplayer {
    background-color: $color-background;
    font-size: 13px;
    height: $player-header-height + $player-list-height;
    width: 100%;
    overflow: hidden;
    position: relative;
    // border: 1px solid rgb(63,75,92);
    color: $color-font;
    .PlayerHeader {
        height: $player-header-height;
        display: flex;
        align-items: center;
        padding: 0 3px;
        .btn-all {
            font-size: 12px;
            font-weight: bold;
            display: flex;
            align-items: center;

            .fas,
            .equalizer {
                margin-right: 6px;
            }
            &:hover {
                color: #fff;
            }
        }
    }

    .PlayerLists {
        // height: $player-list-height;
        overflow: hidden;
        overflow-y: scroll;
        .sentense-item {
            display: flex;
            font-weight: bold;
            padding: 3px 5px;
            align-items: center;
            .equalizer {
                margin-left: 6px;
            }
            cursor: pointer;
            &:hover {
                color: #fff;
            }
            &:last-child {
                border-bottom: 0;
            }
            .no {
                color: $color-font-a;
                margin-right: 8px;
                margin-left: 4px;
            }
        }
    }

    .PlayerLogo {
        position: absolute;
        right: 0;
        top: 0;
        border-bottom-left-radius: 4px;
        padding: 0 5px;
        background-color: #ebebeb;
        // text-align: right;
        width: 200px;
        font-weight: bold;
        font-size: 12px;

        padding-right: 3px;
        .fa-compact-disc {
            position: absolute;
            top: 7px;
            font-size: 16px;
            color: #000;
            z-index: 2;
        }
        .btn-home {
            color: #000;
            text-decoration: none;
            .fas {
                margin-right: 5px;
            }
        }

        &:hover {
            background-color: #fff;
            // font-size: 14px;
        }
    }

    .btn-share {
        position: absolute;
        opacity: 0.5;
        color: #fff;
        bottom: 10px;
        right: 10px;
        font-size: 20px;
        cursor: pointer;
        &:hover {
            opacity: 1;
        }
    }
    .SharePanel {
        padding: 10px;
        position: absolute;
        background-color: $color-background-80;
        width: 100%;
        height: $player-header-height + $player-list-height;
        z-index: 1;
        .btn-back {
            cursor: pointer;
            &:hover {
                color: #fff;
            }
        }
        .btns2 {
            margin-bottom: 5px;
            .btn {
                margin: 0 5px;
                font-weight: bold;
                .fas,
                .far {
                    margin-right: 5px;
                }
                &.btn-link {
                    margin-left: 10px;
                }
                &.active {
                    color: #fff;
                    border-bottom: 1px solid #fff;
                }
            }
        }

        .textarea {
            margin: 0px;
            width: 100%;
            height: 70px;
            opacity: 0.8;
            color: rgb(51, 51, 51);
            font-size: 10px;
        }
    }
}

.marquee {
    overflow: hidden;
    position: relative;
    width: 170px;
    margin-top: 5px;
    display: inline-block;
    margin-left: 20px;
    &p:hover {
        -moz-animation-play-state: paused;
        -webkit-animation-play-state: paused;
        animation-play-state: paused;
    }
    &p:after {
        content: "";
        white-space: nowrap;
        padding-right: 50px;
    }
    p {
        margin: 0;
        padding-left: 100%;
        display: inline-block;
        white-space: nowrap;

        animation-name: marquee;
        animation-timing-function: linear;
        animation-duration: 30s;
        animation-iteration-count: infinite;
    }
}

@keyframes marquee {
    from {
        transform: translate(0%);
    }
    99%,
    to {
        transform: translate(-100%);
    }
}
